// src/app/orders/page.js
'use client'

import React from 'react'
import { useOrders } from '@/context/OrderContext'
import Navbar from '@/components/home/Navbar'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Package, Clock, Truck } from 'lucide-react'
import Image from 'next/image'
import productsData from '@/data/Products.json' // Rasmlarni olish uchun

export default function OrdersPage() {
	const { orders } = useOrders()

	return (
		<div className='min-h-screen bg-gray-50'>
			<Navbar />
			<div className='container mx-auto px-4 py-8'>
				<h1 className='text-2xl font-bold mb-6'>Mening Buyurtmalarim</h1>

				{orders.length === 0 ? (
					<div className='text-center py-20 text-gray-500'>
						<Package className='w-16 h-16 mx-auto mb-4 text-gray-300' />
						<p>Hozircha buyurtmalar yo'q.</p>
					</div>
				) : (
					<div className='space-y-4'>
						{orders.map(order => (
							<Card
								key={order.id}
								className='border-none shadow-sm hover:shadow-md transition'
							>
								<CardContent className='p-6'>
									<div className='flex flex-col md:flex-row justify-between mb-4 border-b pb-4'>
										<div>
											<h3 className='font-bold text-lg'>
												Buyurtma â„–{order.id}
											</h3>
											<p className='text-sm text-gray-500'>{order.date}</p>
										</div>
										<div className='flex items-center gap-2 mt-2 md:mt-0'>
											<Badge className='bg-blue-100 text-blue-800 hover:bg-blue-200'>
												{order.status}
											</Badge>
											<Badge
												variant='outline'
												className='flex items-center gap-1'
											>
												{order.paymentType === 'card' ? 'Karta' : 'Naqd'}
											</Badge>
											<Badge
												variant='outline'
												className='flex items-center gap-1'
											>
												<Truck className='w-3 h-3' />
												{order.deliveryType === 'delivery'
													? 'Yetkazib berish'
													: 'Olib ketish'}
											</Badge>
										</div>
									</div>

									<div className='space-y-4'>
										{order.items.map(item => {
											// Asl mahsulot ma'lumotlarini ID orqali topamiz
											const productInfo = productsData.find(
												p => p.id === item.id
											)
											if (!productInfo) return null

											return (
												<div key={item.id} className='flex items-center gap-4'>
													<div className='relative w-16 h-16 bg-gray-100 rounded border'>
														<Image
															src={productInfo.images[0].imageUrl}
															alt={productInfo.nomi}
															fill
															className='object-contain p-1 mix-blend-multiply'
														/>
													</div>
													<div>
														<p className='font-medium text-sm line-clamp-1'>
															{productInfo.nomi}
														</p>
														<p className='text-gray-500 text-xs'>
															{item.quantity} dona
														</p>
													</div>
												</div>
											)
										})}
									</div>
								</CardContent>
							</Card>
						))}
					</div>
				)}
			</div>
		</div>
	)
}
